<template>
  <div class="search-box">
    <i class="icon-search"></i>
    <input ref="query" class="box" :placeholder="placeholder" v-model="query"/>
    <i class="icon-dismiss" v-show="query" @click="clear"></i>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props:{
      placeholder:{
        type:String,
        default:'搜索歌曲、歌手'
      }
    },
    data(){
      return {
        query:''
      }
    },
    methods:{
      clear(){
        this.query=''
      },
      setQuery(query){//子组件接收父组件的数据
        this.query=query
      }
    },
    created(){
      this.$watch('query',(newQuery)=>{
        this.$emit('query',newQuery)
      })
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/variable"

  .search-box
    display: flex
    align-items: center
    box-sizing: border-box
    width: 100%
    padding: 0 6px
    height: 40px
    background: $color-background
    border-radius: 6px
    .icon-search
      font-size: 24px
      color: $color-theme
    .box
      flex: 1
      margin: 0 5px
      line-height: 18px
      background: $color-background
      color: $color-text
      font-size: $font-size-medium
      &::placeholder
        color: $color-text-d
    .icon-dismiss
      font-size: 16px
      color: $color-theme
</style>


