<!-- 歌手列表组件 -->
<template>
	<scroll :data="data" class="listview" ref="listview">
		<ul>
			<li v-for="group in data" class="list-group" ref="listGroup">
				<!-- <h2 class="list-group-title">{{group.title}}</h2> -->
				<ul>
					<li @click="selectItem(item)" v-for="item in group.items" class="list-group-item">
						<img class="avatar" v-lazy="item.avatar">
						<span class="name">{{item.name}}</span>
					</li>
				</ul>
			</li>
		</ul>
    <loading></loading>
	</scroll>
</template>
<script>
	import scroll from './scroll.vue'
  import loading from '@/base/loading'
	export default{
		props:{
			data:{//接收父组件的歌手列表
				type:Array,
				default:[]
			}
		},
		components:{
			scroll,
      loading
		},
		methods:{
			selectItem(item){
				this.$emit('select',item)
			},
      refresh(){
        this.$refs.listview.refresh()
      }
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/variable"

  .listview
    position: relative
    width: 100%
    height: 100%
    overflow: hidden
    background: $color-background
    top:0
    .list-group
      // padding-bottom: 30px
      background:$color-gray-background
      .list-group-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-d
        background: $color-highlight-background
      .list-group-item
        display: flex
        align-items: center
        padding: 20px 0 0 30px
        .avatar
          width: 50px
          height: 50px
          border-radius: 50%
        .name
          margin-left: 20px
          color: $color-text-d
          font-size: $font-size-medium
    .list-shortcut
      position: absolute
      z-index: 30
      right: 0
      top: 50%
      transform: translateY(-50%)
      width: 20px
      padding: 20px 0
      border-radius: 10px
      text-align: center
      background: $color-background-d
      font-family: Helvetica
      .item
        padding: 3px
        line-height: 1
        color: $color-text-l
        font-size: $font-size-small
        &.current
          color: $color-theme
    .list-fixed
      position: absolute
      top: 0
      left: 0
      width: 100%
      .fixed-title
        height: 30px
        line-height: 30px
        padding-left: 20px
        font-size: $font-size-small
        color: $color-text-l
        background: $color-highlight-background
    .loading-container
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


