import {playMode} from '@/common/js/config'
const state={
	singer:{},
	playing:false,//播放暂停状态
	fullScreen:false,//展开收起状态
	playlist:[],//顺序播放列表
	sequenceList:[],//随机播放列表
	mode:playMode.sequence,//默认为顺序播放
	currentIndex: -1,//当前播放index
	disc:{},//歌单详情页歌曲列表
	topList:{}//排行榜歌曲数据
}
export default state


