<!-- song-list组件为music-list组件的子组件 -->
<!-- 歌曲列表组件，比如是详情页的下半部分 -->
<template>
  <div class="song-list">
    <ul>
     	<li @click="slectItem(song,index)" v-for="(song,index) in songs" class="item">
     		<div class="content">
     			<h2 class="name">{{song.name}}</h2>
     			<p class="desc">{{getDesc(song)}}</p>
     		</div>
     	</li>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props:{
    	songs:{//接收100首歌曲（包含每首歌曲的8条数据）
    		type:Array,
    		default:[]
    	}
    },
    methods:{
    	getDesc(song) {
	        return `${song.singer}·${song.album}`
	    },
	    slectItem(item,index){//
	    	this.$emit('select',item,index)
	    }
    } 
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/variable"
  @import "../common/stylus/mixin"

  .song-list
    .item
      display: flex
      align-items: center
      box-sizing: border-box
      height: 64px
      font-size: $font-size-medium
      .rank
        flex: 0 0 25px
        width: 25px
        margin-right: 30px
        text-align: center
        .icon
          display: inline-block
          width: 25px
          height: 24px
          background-size: 25px 24px
        .text
          color: $color-theme
          font-size: $font-size-large
      .content
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          no-wrap()
          color: $color-text
        .desc
          no-wrap()
          margin-top: 4px
          color: $color-text-d
</style>


