<template>
  <div class="singer" ref="singer">
    <listview @select="selectSinger" :data="singers" ref="list"></listview>
    <router-view></router-view>
  </div>
</template>

<script>
 import {getSingerList} from '@/api/singer'//获取歌手列表函数
 import {ERR_OK} from '@/api/config'
 import Singer from '@/common/js/singer'//引入创建的一个类
 import listview from '@/base/listview'
 import {mapMutations} from 'vuex'
 import {playlistMixin} from '@/common/js/mixin'
 export default{
  mixins:[playlistMixin],
 	components:{
 		listview
 	},
 	data(){
 		return{
 			singers:[]
 		}
 	},
 	created(){
 		getSingerList().then((res)=>{
 			if(res.code===ERR_OK){
 				// console.log(res.data.list)
 				// console.log(this.normalizeSinger(res.data.list))
 				this.singers=this.normalizeSinger(res.data.list)
 			}
 		})
 	},
 	methods:{//格式化歌手列表数据
 		normalizeSinger(list){
 			let map={
 				hot:{
 					title:'热门',
 					items:[]
 				}
 			}
 			list.forEach((item,index)=>{
 				// if(index<10){//热门歌手为10条数据
 				// 	map.hot.items.push(new Singer({
 				// 		name: item.Fsinger_name,
     //        id: item.Fsinger_mid
 				// 	}))
 				// }
 				const key=item.Findex
 				if(!map[key]){
 					map[key]={
 						title:key,
 						items:[]
 					}
 				}
 				map[key].items.push(new Singer({
		            name: item.Fsinger_name,
		            id: item.Fsinger_mid
		        }))
 			})
 			let ret=[]
 			let hot=[]
 			for(let key in map){
 				let val=map[key]
 				if(val.title.match(/[a-zA-Z]/)){
 					ret.push(val)
 				}else if(val.title==='热门'){
 					hot.push(val)
 				}
 			}
 			ret.sort((a, b) => {//按照编码排序a-z
	          	return a.title.charCodeAt(0) - b.title.charCodeAt(0)
	        })
	        return hot.concat(ret)
 		},
 		selectSinger(singer){
 			this.$router.push({
 				path:'/singer/'+singer.id
 			})
      this.setSinger(singer)//提交mutations,修改state
 		},
    ...mapMutations({
      setSinger:'SET_SINGER'
    }),
    handlePlaylist(playlist) {
        const bottom = playlist.length > 0 ? '60px' : ''
        this.$refs.singer.style.bottom = bottom
        this.$refs.list.refresh()
      }
 	  }
 }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  .singer
    position: fixed
    top: 88px
    bottom: 0
    width: 100%
</style>



