<!-- music-list组件为singer-detail组件的子组件 -->
<template>
	<div class="music-list">
	    <div class="back" @click='back'>
	      <i class="icon-back"></i>
	    </div>
	    <!--  -->
	    <h1 class="title" v-html="title"></h1>
	    <div class="bg-image" ref="bgImage" :style="bgStyle">
	      <div class="filter"></div>
	    </div>
	    <div class="bg-layer" ref="layer"></div>
	    <scroll class="list" @scroll="scroll" :data="songs" ref="list" :listen-scroll="listenScroll" :probe-type="probeType">
	    	<div class="song-list-wrapper">
	    		<songlist @select="selectItem" :songs="songs"></songlist>
	    	</div>
	    	<div class="loading-container" v-show="!songs.length">
		    	<loading></loading>
		    </div>
	    </scroll>
	    
	</div>
</template>
<script type="text/ecmascript-6">
	import scroll from '@/base/scroll'
	import songlist from '@/base/song-list'
	import {mapActions} from 'vuex'
	import {playlistMixin} from '@/common/js/mixin'
	import loading from '@/base/loading'
	export default{
		mixins:[playlistMixin],
		components:{
			scroll,
			songlist,
			loading
		},
		props:{//接收父组件传过来的图片，歌手名，所有歌曲
			bgImage:{
				type:String,
				default:''
			},
			title:{
				type:String,
				default:''
			},
			songs:{//接收100首歌曲（包含每首歌曲的8条数据）(歌手页面)
				type:Array,
				default:[]
			}
		},
		// computed:{
		// 	bgStyle(){
		// 		return `background-image:url(${this.bgImage})`
		// 	}
		// },
		data(){
			return {
				bgStyle:{
					backgroundImage:'url('+this.bgImage+')'
				},
				scrollY:0
			}
		},
		mounted(){//歌曲滚动列表的位置为背景图的偏移
			//保存背景图的高度
			this.imageHeight = this.$refs.bgImage.clientHeight
			//layer最大移动高度
      		this.minTransalteY = -this.imageHeight+40
			this.$refs.list.$el.style.top=`${this.$refs.bgImage.clientHeight}px`
		},
		methods:{
			back(){
				this.$router.back()
			},
			scroll(pos){//获取实时滚动的值
				this.scrollY=pos.y
			},
			selectItem(item,index){
				this.selectPlay({
		          	list: this.songs,//传入100条歌曲
		          	index//当前歌曲的下标
		        })	
			},
			...mapActions([
		        'selectPlay'
		    ]),
		    handlePlaylist(playlist) {
		        const bottom = playlist.length > 0 ? '60px' : ''
		        this.$refs.list.$el.style.bottom = bottom
		        this.$refs.list.refresh()
	      	},
		},
		created(){
			this.probeType = 3
      		this.listenScroll = true
		},
		watch:{
			scrollY(newY){//监听scroll组件的偏移量
				let translateY = Math.max(this.minTransalteY, newY)
				this.$refs.layer.style['transform'] = `translate3d(0,${translateY}px,0)`
				this.$refs.layer.style['weblitTransform'] = `translate3d(0,${translateY}px,0)`

				let zIndex = 0
				let scale = 1
				const percent = Math.abs(newY / this.imageHeight)
		        if (newY > 0) {
		          scale = 1 + percent
		          zIndex = 10
		        } 
				if (newY < this.minTransalteY) {
		          zIndex = 10
		          this.$refs.bgImage.style.paddingTop = 0
		          this.$refs.bgImage.style.height = `40px`
		        } else {
		          this.$refs.bgImage.style.paddingTop = '70%'
		          this.$refs.bgImage.style.height = 0
		        }
		        this.$refs.bgImage.style.zIndex = zIndex
		        this.$refs.bgImage.style.zIndex = zIndex
		        this.$refs.bgImage.style['transform'] = `scale(${scale})`
		        this.$refs.bgImage.style['weblitTransform'] = `scale(${scale})`
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"

  .music-list
    position: fixed
    z-index: 100
    top: 0
    left: 0
    bottom: 0
    right: 0
    background: $color-background
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .title
      position: absolute
      top: 0
      left: 10%
      z-index: 40
      width: 80%
      no-wrap()
      text-align: center
      line-height: 40px
      font-size: $font-size-large
      color: $color-text
    .bg-image
      position: relative
      width: 100%
      height: 0
      padding-top: 70%
      transform-origin: top
      background-size: cover
      .play-wrapper
        position: absolute
        bottom: 20px
        z-index: 50
        width: 100%
        .play
          box-sizing: border-box
          width: 135px
          padding: 7px 0
          margin: 0 auto
          text-align: center
          border: 1px solid $color-theme
          color: $color-theme
          border-radius: 100px
          font-size: 0
          .icon-play
            display: inline-block
            vertical-align: middle
            margin-right: 6px
            font-size: $font-size-medium-x
          .text
            display: inline-block
            vertical-align: middle
            font-size: $font-size-small
      .filter
        position: absolute
        top: 0
        left: 0
        width: 100%
        height: 100%
        background: rgba(255, 255, 255, 0.3)
    .bg-layer
      position: relative
      height: 100%
      background: $color-background
    .list
      position: fixed
      top: 0
      bottom: 0
      width: 100%
      background: $color-background
      .song-list-wrapper
        padding: 20px 30px
        background: $color-gray-background
      .loading-container
        position: absolute
        width: 100%
        top: 50%
        transform: translateY(-50%)
</style>


