<template>
  <scroll class="suggest" :data="result" :pullup="pullup" @scrollToEnd="searchMore" ref="suggest">
    <ul class="suggest-list">
      <li class="suggest-item" v-for="item in result" @click="selectItem(item)">
        <div class="icon">
          <i :class="getIconCls(item)"></i>
        </div>
        <div class="name">
          <p class="text" v-html="getDisplayName(item)"></p>
        </div>
      </li>
      <loading v-show="hasMore"></loading>
    </ul>
  </scroll>
</template>
<script>
  import {search} from '@/api/search'
  import {ERR_OK} from '@/api/config'
  import {createSong} from '@/common/js/song'
  import {getVkey} from '@/api/song'
  import scroll from '@/base/scroll'
  import loading from '@/base/loading'
  import Singer from '@/common/js/singer'
  import {mapMutations,mapActions} from 'vuex'
  export default{
    components:{
      scroll,
      loading
    },
    props:{
      query:{//接收检索词
        type:String,
        default:''
      },
      showSinger:{
        type:Boolean,
        default:true
      }
    },
    data(){
      return {
        page:1,//默认检索第一页
        result:[],
        pullup:true,
        hasMore:true
      }
    },
    methods:{
      search(){
        this.hasMore=true
        this.page = 1
        // this.$refs.suggest.scrollTo(0, 0)
        let ret=[]
        search(this.query,this.page,this.showSinger).then((res)=>{
          if(res.code===ERR_OK){
            this._checkMore(res.data)
            return res.data
          }
        }).then((data)=>{
          if(data.zhida&&data.zhida.singerid){
            ret.push({...data.zhida,...{type:'singer'}})
          }
          data.song.list.forEach((musicData)=>{
            getVkey(musicData.songmid).then((res)=>{
              return res.data.items
            }).then((res)=>{
              if (musicData.songid && musicData.albummid) {
                ret.push(createSong(musicData,res))
              }
            })
          })
          this.result=ret
        })
      },
      // _genResult(data){
      //   let ret=[]
      //   if(data.zhida&&data.zhida.singerid){
      //     ret.push({...data.zhida,...{type:'singer'}})
      //   }
      //   if(data.song){
      //     ret=ret.concat(this.normalizeSongs(data.song.list))
      //   }
      //   return ret
      // },
      // normalizeSongs(list){
      //   let ret=[]
      //   list.forEach((musicData)=>{
      //     getVkey(musicData.songmid).then((res)=>{
      //       return res.data.items
      //     }).then((res)=>{
      //       if (musicData.songid && musicData.albummid) {
      //         ret.push(createSong(musicData,res))
      //       }
      //     })
          
      //   })
      //   return ret
      // },
      getIconCls(item){
        if(item.type==='singer'){
          return 'icon-mine'
        }else{
          return 'icon-music'
        }
      },
      getDisplayName(item) {
        if (item.type === 'singer') {
          return item.singername
        } else {
          return `${item.name}-${item.singer}`
        }
      },
      
      searchMore(){
        if(!this.hasMore){
          return
        }
        this.page++
        search(this.query,this.page,this.showSinger).then((res)=>{
          if(res.code===ERR_OK){
            this._checkMore(res.data)
            return res.data
          }
          
        }).then((data)=>{
          data.song.list.forEach((musicData)=>{
            getVkey(musicData.songmid).then((res)=>{
              return res.data.items
            }).then((res)=>{
              if (musicData.songid && musicData.albummid) {
                this.result.push(createSong(musicData,res))
              }
            })
          })
        })
      },
      _checkMore(data) {
        const song = data.song
        if (!song.list.length || (song.curnum + song.curpage * 20) > song.totalnum) {
          this.hasMore = false
        }
      },
      selectItem(item){
        if(item.type==='singer'){
          const singer = new Singer({
            id: item.singermid,
            name: item.singername
          })
          this.$router.push({
            path: `/search/${singer.id}`
          })
          this.setSinger(singer)
        }else{
          this.insertSong(item)
        }

      },
      ...mapMutations({
          setSinger:'SET_SINGER'
      }),
      ...mapActions([
          'insertSong'
      ]),
      refresh(){
        this.$refs.suggest.refresh()
      }
    },
    watch:{
      query(){//监听搜索关键词变化
        this.search()
      }
    },
  }
</script>


<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"

  .suggest
    height: 100%
    overflow: hidden
    .suggest-list
      padding: 0 30px
      .suggest-item
        display: flex
        align-items: center
        padding-bottom: 20px
      .icon
        flex: 0 0 30px
        width: 30px
        [class^="icon-"]
          font-size: 14px
          color: $color-text-d
      .name
        flex: 1
        font-size: $font-size-medium
        color: $color-text-d
        overflow: hidden
        .text
          no-wrap()
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>


