<template>
	<transition name="slide">
		<!-- <div class="singer-detail"></div> -->
    <musiclist :songs="songs" :title="title" :bgImage="bgImage"></musiclist>
	</transition>
</template>
<script>
  import {mapGetters} from 'vuex'
  import {getSingerDetail} from '@/api/singer'
  import {ERR_OK} from '@/api/config'
  import {createSong} from '@/common/js/song'
  import {getVkey} from '@/api/song'
  import musiclist from '@/components/music-list/music-list'
  export default{
    data(){
      return {
        songs:[]//100首歌曲
      }
    },
    components:{
      musiclist
    },  
    computed:{
      ...mapGetters([
        'singer'//singer里面保存着id，name，avatar
      ]),
      title(){
        return this.singer.name
      },
      bgImage(){
        return this.singer.avatar
      }
    },
    created(){
      console.log(this.singer)//注意是this.singer，输出的是state里的数据
      if(!this.singer.id){
        this.$router.back()
      }
      //传入歌手id，获取歌手的歌曲
      getSingerDetail(this.singer.id).then((res)=>{
        if(res.code===ERR_OK){
          return res.data.list
        }
      }).then((res)=>{
          let ret=[]
          res.forEach((item)=>{
            let {musicData}=item//对象的解构赋值
            getVkey(musicData.songmid).then((res)=>{
              return res.data.items
            }).then((res)=>{
              if (musicData.songid && musicData.albummid) {
                ret.push(createSong(musicData,res))
              }
            })
          })
          this.songs=ret
          // console.log(this.songs)
      })
      
    },
    // methods:{
    //   normalizeSongs(list){
    //     let ret=[]
    //     list.forEach((item)=>{
    //       let {musicData}=item//对象的解构赋值
    //       getVkey(musicData.songmid).then((res)=>{
    //         return res.data.items
    //       }).then((res)=>{
    //         if (musicData.songid && musicData.albummid) {
    //           ret.push(createSong(musicData,res))
    //         }
    //       })
          
    //     })
    //     return ret
    //   }
    // }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  
  // .singer-detail
  //   position:fixed
  //   z-index:100
  //   top:0
  //   left:0
  //   right:0
  //   bottom:0
  //   background:$color-background
  
  .slide-enter-active,.slide-leave-active
  	transition:all 0.3s ease
  	
  .slide-enter,.slide-leave-active
  	transform:translate3d(100%,0,0)

</style>


