<template>
  <!-- 进度条 -->
	<div class="progress-bar" ref="progressBar" @click="progressClick">
	    <div class="bar-inner">
          <!-- 移动的进度条 -->
	      	<div class="progress" ref="progress" ></div>
          <!-- 移动的小球 -->
		      <div class="progress-btn-wrapper" ref="progressBtn"
            @touchstart.prevent="progressTouchStart"
           @touchmove.prevent="progressTouchMove"
           @touchend="progressTouchEnd">
		        <div class="progress-btn"></div>
	      	</div>
	    </div>
	</div>
</template>
<script>
  const progressBtnWidth=16
	export default{
    props:{
      percent:{//歌曲播放的进度比例
        type:Number,
        default:0
      }
    },
    watch:{
      percent(newPercent){
        if(newPercent>=0&&!this.touch.initiated){//并且在没有拖动的过程中，才去设置进度条走动
          const barWidth=this.$refs.progressBar.clientWidth-progressBtnWidth//總長度-球的寬度
          const offsetWidth=newPercent*barWidth
          // // 设置出滚动条的偏移
          // this.$refs.progress.style.width=`${offsetWidth}px`
          // // 设置出小球的偏移
          // this.$refs.progressBtn.style['transform']=`translate3d(${offsetWidth}px,0,0)`
          // this.$refs.progressBtn.style['webkitTransform']=`translate3d(${offsetWidth}px,0,0)`
          // 封装成函数
          this._offset(offsetWidth)
        }
      }
    },
    created() {
      this.touch = {}//作為共享數據
    },
    methods:{
      progressTouchStart(e){
        this.touch.initiated = true//标志位，初始化
        this.touch.startX = e.touches[0].pageX//手指按下的偏移
        this.touch.left = this.$refs.progress.clientWidth//移动的滚动条的宽度
      },
      progressTouchMove(e){
        if (!this.touch.initiated) {
          return
        }
        const deltaX = e.touches[0].pageX - this.touch.startX//计算出拖动的距离
        const offsetWidth = Math.min(this.$refs.progressBar.clientWidth - progressBtnWidth, Math.max(0, this.touch.left + deltaX))
        this._offset(offsetWidth)
      },
      progressTouchEnd() {
        this.touch.initiated = false
        this._triggerPercent()
      },
      _offset(offsetWidth) {
        // 设置出滚动条的偏移
          this.$refs.progress.style.width=`${offsetWidth}px`
          // 设置出小球的偏移
          this.$refs.progressBtn.style['transform']=`translate3d(${offsetWidth}px,0,0)`
          this.$refs.progressBtn.style['webkitTransform']=`translate3d(${offsetWidth}px,0,0)`
      },
      progressClick(e){//点击之后改变播放进度
        const rect = this.$refs.progressBar.getBoundingClientRect()
        const offsetWidth = e.pageX - rect.left
        this._offset(offsetWidth)
        this._triggerPercent()
      },
      _triggerPercent() {
        const barWidth = this.$refs.progressBar.clientWidth - progressBtnWidth
        const percent = this.$refs.progress.clientWidth / barWidth//拖动之后计算出比例
        this.$emit('percentChange', percent)//将比例传给player组件
      },
    }
  }
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../common/stylus/variable"

  .progress-bar
    height: 30px
    .bar-inner
      position: relative
      top: 13px
      height: 4px
      background: rgba(0, 0, 0, 0.3)
      .progress
        position: absolute
        height: 100%
        background: $color-theme
      .progress-btn-wrapper
        position: absolute
        left: -8px
        top: -13px
        width: 30px
        height: 30px
        .progress-btn
          position: relative
          top: 7px
          left: 7px
          box-sizing: border-box
          width: 16px
          height: 16px
          border: 3px solid $color-background
          border-radius: 50%
          background: $color-theme
</style>


