<template>
	<div class="player" v-show="playlist.length>0">
		<transition name="normal">
			<div class="normal-player" v-show="fullScreen">
	          <div class="background">
	            <img width="100%" height="100%" :src="currentSong.image">
	          </div>
	          <div class="top">
	            <div class="back" @click="back">
	              <i class="icon-back"></i>
	            </div>
	            <h1 class="title" v-html="currentSong.name"></h1>
	            <h2 class="subtitle" v-html="currentSong.singer"></h2>
	          </div>
	          <div class="middle">
	            <div class="middle-l" ref="middleL">
	              <div class="cd-wrapper" ref="cdWrapper">
	                <div class="cd" :class="cdCls">
	                  <img class="image" :src="currentSong.image">
	                </div>
	              </div>
	              <div class="playing-lyric-wrapper">
	                <!-- <div class="playing-lyric">{{playingLyric}}</div> -->
	              </div>
	            </div>
	            <!-- <scroll class="middle-r" ref="lyricList" :data="currentLyric && currentLyric.lines">
	              <div class="lyric-wrapper">
	                <div v-if="currentLyric">
	                  <p ref="lyricLine"
	                     class="text"
	                     :class="{'current': currentLineNum ===index}"
	                     v-for="(line,index) in currentLyric.lines">{{line.txt}}</p>
	                </div>
	              </div>
	            </scroll> -->
	          </div>
	          <div class="bottom">
	            <div class="dot-wrapper">
	              <!-- <span class="dot" :class="{'active':currentShow==='cd'}"></span> -->
	              <!-- <span class="dot" :class="{'active':currentShow==='lyric'}"></span> -->
	            </div>
	            <div class="progress-wrapper">
	              <span class="time time-l">{{format(currentTime)}}</span>
	              <div class="progress-bar-wrapper">
	                <progress-bar :percent="percent" @percentChange="onProgressBarChange"></progress-bar>
	              </div>
	              <span class="time time-r">{{format(currentSong.duration)}}</span>
	            </div>
	            <div class="operators">
	              <!-- <div class="icon i-left">
	                <i class="icon-sequence"></i>
	              </div> -->
	              <div class="icon i-left" >
	                <i @click="prev" class="icon-prev"></i>
	              </div>
	              <div class="icon i-center">
	                <i @click="togglePlaying" :class="playIcon" ref="center"></i>
	              </div>
	              <div class="icon i-right">
	                <i @click="next" class="icon-next"></i>
	              </div>
	              <!-- <div class="icon i-right">
	                <i class="icon icon-not-favorite"></i>
	              </div> -->
	            </div>
	          </div>
      		</div>
		</transition>
		<transition name="normal">
			<div class="mini-player" v-show="!fullScreen" @click="open">
		        <div class="icon">
		            <img :class="cdCls" width="40" height="40" :src="currentSong.image">
		          </div>
		          <div class="text">
		            <h2 class="name" v-html="currentSong.name"></h2>
		            <p class="desc" v-html="currentSong.singer"></p>
		          </div>
		          <div class="control" @click.stop="togglePlaying">
		            <i :class="miniIcon"></i>
		          </div>
		          <!-- <div class="control" >
		            <i class="icon-playlist"></i>
		          </div> -->
		    </div>
		</transition>
		<audio autoplay ref="audio" :src="currentSong.url" @canplay="ready" @error="error" @timeupdate="updateTime" @ended="end"></audio>
	</div>
</template>
<script>
	import {mapGetters,mapMutations} from 'vuex'
	import progressBar from '@/base/progress-bar'
	export default{
		components:{
			progressBar
		},
		data(){
			return {
				songReady:false,
				currentTime:''
			}
		},
		computed:{
			...mapGetters([
				'fullScreen',
				'playlist',
				'currentSong',
				'playing',
				'currentIndex'
			]),
			playIcon(){//播放界面播放暫停樣式按鈕切換
				return this.playing?'icon-pause':'icon-play'
			},
			miniIcon() {//迷你界面播放暫停樣式按鈕切換
		        return this.playing ? 'icon-pause-mini' : 'icon-play-mini'
		    },
		    cdCls() {
		        return this.playing ? 'play' : 'play pause'
		    },
		    percent(){//計算出當前播放的比例，并傳給子組件
		    	return this.currentTime/this.currentSong.duration
		    }
		},
		methods:{
			back(){
				//this.fullScreen=false不能直接修改成false
				this.setFullScreen(false)//关闭播放界面
			},
			open(){
				this.setFullScreen(true)//打开迷你播放器
			},
			togglePlaying(){
        // console.log(1)
        this.setPlayingState(!this.playing)//播放暂停切换
        // this.$refs.center.addEventListener('click',()=>{
        //     this.$refs.audio.play()
        //     console.log(2)
        // })
				
			},
			next(){
				if(!this.songReady){
					return
				}
				let index=this.currentIndex+1
				if(index===this.playlist.length){
					index=0
				}
				this.setCurrentIndex(index)
				if(!this.playing){//當暫停狀態，點擊下一首歌，變換播放按鈕狀態
					this.togglePlaying()
				}
				this.songReady=false
			},
			prev(){
				if(!this.songReady){
					return
				}
				let index=this.currentIndex-1
				if(index===-1){
					index=this.playlist.length-1
				}
				this.setCurrentIndex(index)
				if(!this.playing){
					this.togglePlaying()
				}
				this.songReady=false
			},
			ready(){//歌曲是否加载完成的判断
		        this.songReady=true
		    },
		    error(){//當歌曲加載失敗
		    	this.songReady=true
		    },
		    updateTime(e){//當前播放時間
		    	this.currentTime=e.target.currentTime
		    },
		    end(){
		    	this.next()
		    },
		    format(time){//
		    	time=time|0//向下取整，相當於Math.floor()
		    	let minute=time/60|0
		    	let second=time%60
		    	second=second>=10?second:'0'+second
		    	return `${minute}:${second}`
		    },
		    onProgressBarChange(percent){//接收子组件的拖动比例，计算出当前的播放时间
		    	this.$refs.audio.currentTime=this.currentSong.duration*percent
		    	if(!this.playing){//如果拖动的时候是暂停，拖动之后使其自动播放
		    		this.togglePlaying()
		    	}
		    },
			...mapMutations({
				setFullScreen:'SET_FULL_SCREEN',//mutation-types里的方法
				setPlayingState:'SET_PLAYING_STATE',
				setCurrentIndex:'SET_CURRENT_INDEX'
			})
		},
		watch:{
			// currentSong(){//监听当前歌曲变化
			// 	this.$nextTick(()=>{
			// 		this.$refs.audio.play()
			// 	})
			// },
      currentSong(newSong, oldSong) {
        if (!newSong.id || !newSong.url || newSong.id === oldSong.id) {
          return
        }
        this.songReady = false
        setTimeout(()=>{ 
          this.$refs.audio.src = newSong.url
          this.$refs.audio.play()
        }, 0)
        
      },
			playing(newPlaying){//监听state里面playing状态的改变
				this.$nextTick(()=>{
					newPlaying?this.$refs.audio.play():this.$refs.audio.pause()
          // alert(this.$refs.audio.paused)
				})

			},

		}
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "../../common/stylus/variable"
  @import "../../common/stylus/mixin"

  .player
    .normal-player
      position: fixed
      left: 0
      right: 0
      top: 0
      bottom: 0
      z-index: 150
      background: #aaa
      .background
        position: absolute
        left: 0
        top: 0
        width: 100%
        height: 100%
        z-index: -1
        opacity: 0.6
        filter: blur(20px)
      .top
        position: relative
        margin-bottom: 25px
        .back
          position absolute
          top: 0
          left: 6px
          z-index: 50
          .icon-back
            display: block
            padding: 9px
            font-size: $font-size-large-x
            color: $color-theme
            transform: rotate(-90deg)
        .title
          width: 70%
          margin: 0 auto
          line-height: 40px
          text-align: center
          no-wrap()
          font-size: $font-size-large
          color: $color-text
        .subtitle
          line-height: 20px
          text-align: center
          font-size: $font-size-medium
          color: $color-text
      .middle
        position: fixed
        width: 100%
        top: 80px
        bottom: 170px
        white-space: nowrap
        font-size: 0
        .middle-l
          display: inline-block
          vertical-align: top
          position: relative
          width: 100%
          height: 0
          padding-top: 80%
          .cd-wrapper
            position: absolute
            left: 10%
            top: 0
            width: 80%
            height: 100%
            .cd
              width: 100%
              height: 100%
              box-sizing: border-box
              border: 10px solid rgba(255, 255, 255, 0.1)
              border-radius: 50%
              &.play
                animation: rotate 20s linear infinite
              &.pause
                animation-play-state: paused
              .image
                position: absolute
                left: 0
                top: 0
                width: 100%
                height: 100%
                border-radius: 50%

          .playing-lyric-wrapper
            width: 80%
            margin: 30px auto 0 auto
            overflow: hidden
            text-align: center
            .playing-lyric
              height: 20px
              line-height: 20px
              font-size: $font-size-medium
              color: $color-text-l
        .middle-r
          display: inline-block
          vertical-align: top
          width: 100%
          height: 100%
          overflow: hidden
          .lyric-wrapper
            width: 80%
            margin: 0 auto
            overflow: hidden
            text-align: center
            .text
              line-height: 32px
              color: $color-text-l
              font-size: $font-size-medium
              &.current
                color: $color-text
      .bottom
        position: absolute
        bottom: 50px
        width: 100%
        .dot-wrapper
          text-align: center
          font-size: 0
          .dot
            display: inline-block
            vertical-align: middle
            margin: 0 4px
            width: 8px
            height: 8px
            border-radius: 50%
            background: $color-text-l
            &.active
              width: 20px
              border-radius: 5px
              background: $color-text-ll
        .progress-wrapper
          display: flex
          align-items: center
          width: 80%
          margin: 0px auto
          padding: 10px 0
          .time
            color: $color-background
            font-size: $font-size-small
            flex: 0 0 30px
            line-height: 30px
            width: 30px
            &.time-l
              text-align: left
            &.time-r
              text-align: right
          .progress-bar-wrapper
            flex: 1
        .operators
          display: flex
          align-items: center
          .icon
            flex: 1
            color: $color-theme
            &.disable
              color: $color-theme-d
            i
              font-size: 30px
          .i-left
            text-align: right
          .i-center
            padding: 0 20px
            text-align: center
            i
              font-size: 40px
          .i-right
            text-align: left
          .icon-favorite
            color: $color-sub-theme
      &.normal-enter-active, &.normal-leave-active
        transition: all 0.4s
        .top, .bottom
          transition: all 0.4s cubic-bezier(0.86, 0.18, 0.82, 1.32)
      &.normal-enter, &.normal-leave-to
        opacity: 0
        .top
          transform: translate3d(0, -100px, 0)
        .bottom
          transform: translate3d(0, 100px, 0)
    .mini-player
      display: flex
      align-items: center
      position: fixed
      left: 0
      bottom: 0
      z-index: 180
      width: 100%
      height: 60px
      background: #ccc
      &.mini-enter-active, &.mini-leave-active
        transition: all 0.4s
      &.mini-enter, &.mini-leave-to
        opacity: 0
      .icon
        flex: 0 0 40px
        width: 40px
        padding: 0 10px 0 20px
        img
          border-radius: 50%
          &.play
            animation: rotate 10s linear infinite
          &.pause
            animation-play-state: paused
      .text
        display: flex
        flex-direction: column
        justify-content: center
        flex: 1
        line-height: 20px
        overflow: hidden
        .name
          margin-bottom: 2px
          no-wrap()
          font-size: $font-size-medium
          color: $color-text
        .desc
          no-wrap()
          font-size: $font-size-small
          color: $color-text-d
      .control
        flex: 0 0 30px
        width: 30px
        padding: 0 10px
        .icon-play-mini, .icon-pause-mini, .icon-playlist
          font-size: 30px
          color: $color-theme
        .icon-mini
          font-size: 32px
          position: absolute
          left: 0
          top: 0

  @keyframes rotate
    0%
      transform: rotate(0)
    100%
      transform: rotate(360deg)
</style>


